.hero .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin: 4rem auto;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    width: 100%;
    background-color: #032966;
}

.hero .left {
    padding: 1rem;
    width: 350px;
    margin-left: 200px;
    margin-bottom: 100px;
}

.left .leaders-left .leader2 {
    border-bottom: 0.5px solid;
    color: gray;
    position: relative;
    left: 129px;
    top: 12px;
    width: 58px;
    transform: rotate(40deg);
}

.left .leaders-left .leader {
    border-bottom: 0.5px solid;
    color: gray;
    position: relative;
    top: 30px;
    left: 180px;
    width: 200px;
}

.right .leaders-right .leader2 {
    border-bottom: 0.5px solid;
    color: gray;
    position: relative;
    left: 129px;
    top: 12px;
    width: 58px;
    transform: rotate(40deg);
}

.right .leaders-right .leader {
    border-bottom: 0.5px solid;
    color: gray;
    position: relative;
    top: 30px;
    left: 180px;
    width: 200px;
}

.right .leaders-right {
    transform: scaleX(-1);
}

.hero .right {
    padding: 1rem;
    width: 350px;
    margin-bottom: 350px;
}

.hero h4 {
    padding: 1rem 0;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    border-bottom: 2px solid;
    text-align: start;
    font-size: 16px;
}

.hero p {
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    text-align: start;
}

.hero .input-container {
    padding: 1rem;
}

.center .img-container {
    width: 100%;
    display: flex;
}

.hero img {
    max-width: 80%;
    margin: auto;
}

@media screen and (max-width: 600px) {
    .hero .container {
        grid-template-columns: 1fr;
        margin: 1rem 0;
        justify-content: center;
    }
    .hero img {
        max-width: 90%;
        margin: auto;
    }
    .hero p {
        font-size: 15px;
    }
    .hero h4 {
        font-size: 15px;
    }
    .hero .right {
        display: none;
    }
    .hero .left {
        padding: 3rem;
        width: 100%;
        margin-left: 0;
        margin-bottom: 0;
    }
    .right .leaders-right {
        display: none;
    }
    .left .leaders-left {
        display: none;
    }
}

@media screen and (max-width: 1290px) {
    .hero img {
        max-width: 100%;
        margin: auto;
    }
    .hero p {
        font-size: 15px;
    }
    .hero h4 {
        font-size: 15px;
    }
    .hero .left {
        padding: 3rem;
        width: 100%;
        margin-left: 0;
        margin-bottom: 0;
    }
    .right .leaders-right .leader {
        top: 30px;
        left: 180px;
        width: 130px;
    }
    .left .leaders-left .leader {
        top: 30px;
        left: 180px;
        width: 130px;
    }
}

@media screen and (max-width: 990px) {
    .hero .container {
        grid-template-columns: repeat(2, 1fr);
        margin: 1rem 0;
        justify-content: center;
    }
    .hero img {
        max-width: 80%;
        margin: auto;
        margin-top: 120px;
    }
    .hero p {
        font-size: 15px;
    }
    .hero h4 {
        font-size: 15px;
    }
    .hero .left {
        padding: 3rem;
        width: 100%;
        margin-left: 0;
        margin-bottom: 0;
    }
    .right .leaders-right .leader {
        top: 30px;
        left: 180px;
        width: 130px;
    }
    .left .leaders-left .leader {
        top: 30px;
        left: 180px;
        width: 100px;
    }
    .hero .right {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .hero .container {
        grid-template-columns: repeat(1, 1fr);
        margin: 1rem 0;
        justify-content: center;
    }
    .hero img {
        max-width: 80%;
        margin: auto;
        margin-top: -80px;
    }
    .hero p {
        font-size: 15px;
    }
    .hero h4 {
        font-size: 15px;
    }
    .hero .left {
        padding: 3rem;
        width: 100%;
        margin-left: 0;
        margin-bottom: 0;
    }
    .right .leaders-right .leader {
        top: 30px;
        left: 180px;
        width: 130px;
    }
    .left .leaders-left .leader {
        top: 30px;
        left: 180px;
        width: 100px;
    }
    .hero .right {
        display: none;
    }
}