.about-us {
    height: 60vh;
    padding: 4em 5em;
    position: relative;
    height: 600px;
    display: flex;
    justify-content: center;
}

.about-us p {
    width: 50%;
    line-height: 2em;
    color: #011d40;
    position: relative;
}

.info {
    position: relative;
    width: 100%;
    padding: 0em 6em;
}

.info .heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    font-weight: 400;
}

.info p {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}

.table {
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: start;
    margin-left: 20px;
}

.current-container {
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
}

.current-container .header {
    font-size: 12px;
    font-family: 'Montserrat', sans-serif;
    margin-top: 4px;
}

.comming-container .header {
    font-size: 12px;
    font-family: 'Montserrat', sans-serif;
    margin-top: 4px;
}

.comming-container {
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-left: 100px;
}

.current {
    border-top: 1px solid #707070;
    border-bottom: 1px solid #707070;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 130%;
}

.current p {
    width: 50%;
    color: #199bdc;
    font-weight: bold;
    margin: 0.2em 0;
    text-align: center;
    font-weight: 500;
}

.comming-soon {
    border-top: 1px solid #707070;
    border-bottom: 1px solid #707070;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 150%;
}

.comming-soon p {
    width: 50%;
    color: #707070;
    font-weight: bold;
    margin: 0.2em 0;
    text-align: center;
    font-weight: 300;
    opacity: 0.7;
}

.bull-icon {
    height: 6em;
    position: absolute;
    bottom: 60%;
    right: 15%;
}

.bear-icon {
    height: 5.5em;
    position: absolute;
    bottom: 40%;
    right: 29%;
}

.bottom-triangle {
    bottom: -130px;
    left: 0px;
    position: absolute;
    background: #021e3f;
    height: 10em;
    width: 35em;
    transform: skewY(13deg);
    z-index: -1;
}

@media screen and (max-width: 1250px) {
    .info .heading {
        font-size: 17px;
    }
    .info p {
        font-size: 13px;
    }
    .table {
        width: 100%;
        margin-top: 50px;
    }
}

@media screen and (max-width: 1000px) {
    .info {
        padding: 0em 0em;
    }
    .info .heading {
        font-size: 16px;
    }
    .info p {
        font-size: 12px;
    }
    .about-us {
        height: 600px;
    }
    .bull-icon {
        height: 4.5em;
        position: absolute;
        bottom: 70%;
        right: 15%;
    }
    .bear-icon {
        height: 4em;
        position: absolute;
        bottom: 50%;
        right: 29%;
    }
    .table {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
}

@media screen and (max-width: 600px) {
    .info {
        padding: 0em 2em;
        margin: 0;
    }
    .comming-container {
        margin-left: 0;
    }
    .table {
        width: 60%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0em 3em;
    }
    .table .current-container .current {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 250px;
    }
    .table .current-container .current p {
        font-size: 18px;
    }
    .table .comming-container {
        padding-top: 10px;
    }
    .table .comming-container .comming-soon {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        width: 320px;
    }
    .table .comming-container .comming-soon p {
        font-size: 18px;
        font-weight: 500;
        color: #03275e;
    }
    .info {
        width: 100%;
    }
    .bull-icon {
        top: 14%;
        right: -8%;
        height: 7em;
    }
    .bear-icon {
        top: 18%;
        left: -4%;
        height: 5.5em;
    }
    .about-us {
        height: 100vh;
    }
    .current {
        width: 100%;
    }
    .about-us {
        width: 100%;
        padding: 3em 0em;
        height: 1250px;
    }
    .about-us p {
        width: 100%;
    }
    .info {
        width: 100%;
        top: 30%;
    }
    .table {
        width: 100%;
        margin-left: 0px;
    }
    .current {
        width: 100%;
    }
    .current p {
        margin: 0.5em 0;
        width: auto;
    }
    .comming-soon h1 {
        width: 50%;
    }
    .bottom-triangle {
        bottom: -70px;
    }
}