    footer {
        background: black;
        margin: 0 auto;
        text-align: center;
        padding: 1em;
        height: 212px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .footer-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    
    .footer-container img {
        height: 3em;
        position: relative;
        top: 10px;
    }
    
    .btns {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        position: relative;
        top: 40px;
    }
    
    footer button {
        background: none;
        border: none;
        color: white;
        cursor: pointer;
    }
    
    footer p {
        color: white;
        font-size: 0.8em;
        margin: 0;
    }
    
    @media screen and (max-width: 600px) {
        footer {
            padding: 3em 0;
        }
        .footer-container img {
            height: 2em;
            position: relative;
            top: 10px;
        }
        footer button {
            margin-top: 1.5em;
        }
    }