    .container {
        height: 650px;
        background: #199bdc;
        overflow: hidden;
    }
    
    .container .border-div {
        transform: rotate(-13deg) translate(-0.5em);
        transform-origin: left;
        width: 110%;
    }
    
    .box {
        height: 85%;
        transform: skewY(-13deg);
        transform-origin: left;
        position: relative;
        overflow: hidden;
    }
    
    .box:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(#172e67, #021e3f);
        transform: skewY(13deg);
        transform-origin: left;
    }
    
    .inner-text {
        position: relative;
        top: -80%;
        left: 10%;
    }
    
    .container .side-text {
        position: relative;
        top: -80%;
        width: 50%;
        left: 10%;
        font-weight: 600;
        font-family: 'Montserrat', sans-serif;
        font-size: 1.9em;
    }
    
    .container p {
        margin: 0;
        margin-bottom: 1em;
        font-size: 1.9em;
        color: white;
    }
    
    .inner-text img {
        height: 6em;
    }
    
    .inner-text p {
        font-size: 1.5em;
    }
    
    .mobile {
        top: 120px;
        left: 60%;
        height: 750px;
        position: absolute;
    }
    
    @media screen and (max-width: 900px) {
        .mobile {
            top: 0;
            left: 55%;
            top: 27vh;
            height: 80vh;
            padding-right: 50px;
        }
        .inner-text img {
            height: 4em;
        }
    }
    
    .mobile img {
        height: 100%;
    }
    
    .subscribe-container {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        left: 33vw;
        top: -70%;
        width: 22vw;
        justify-content: end;
    }
    
    .stores {
        border: 1px solid black;
        border-radius: 10px;
        width: 55%;
        display: flex;
        justify-content: space-around;
        padding: 0.5em 0;
    }
    
    .stores img {
        width: 40%;
        display: inline-block;
    }
    
    .subscribe-container p {
        flex: 100%;
        color: white;
        font-size: 0.9em;
        text-align: start;
        font-family: 'Avenir', Helvetica, sans-serif;
    }
    
    .submit-mail-container {
        width: 100%;
    }
    
    .submit-mail-form {
        background-color: white;
        padding: 0.3em;
        margin: 0.3em;
        border-radius: 5px;
        display: flex;
        justify-content: space-between;
        height: 2.5em;
    }
    
    .submit-mail-form input {
        border: none;
    }
    
    .submit-mail-form input[type='email'] {
        width: 70%;
        height: 100%;
    }
    
    .submit-mail-form input[type='email']:focus {
        outline: none;
    }
    
    .submit-mail-form input[type='submit'] {
        background-color: #032966;
        width: 30%;
        color: white;
        border-radius: 5px;
    }
    
    @media screen and (max-width: 900px) {
        .subscribe-container {
            width: 40vw;
            left: 25vw;
        }
        .container .side-text {
            z-index: 0;
            top: 30vh;
            left: 0;
            padding: 0 2em;
            width: 100%;
            font-size: 1.3em;
        }
    }
    
    @media screen and (max-width: 500px) {
        .container {
            height: 100vh;
        }
        .box {
            height: 70vh;
        }
        .inner-text {
            z-index: 0;
            left: 0;
            width: 100%;
            top: -80%;
            text-align: center;
        }
        .inner-text img {
            width: 80%;
            height: 100%;
        }
        .inner-text p {
            font-size: 0.9rem;
            margin-top: 10px;
            margin-left: 40px;
            margin-right: 40px;
        }
        .container .side-text {
            z-index: 0;
            top: -50%;
            left: 0;
            padding: 0 2em;
            width: 100%;
            font-size: 1.1em;
            text-align: start;
            font-weight: 400;
            font-family: 'Montserrat', sans-serif;
        }
        .mobile {
            position: block;
            top: 35vh;
            height: 85vh;
            left: 2vw;
            padding: 0;
            margin: 0;
        }
        .mobile img {
            padding: 0;
            margin: 0;
            object-fit: cover;
            width: 70%;
            object-position: 20% 0;
            float: right;
            position: relative;
        }
        .subscribe-container {
            width: 60%;
            left: 5vw;
            top: -22%;
        }
        .subscribe-container p {
            font-size: 11px;
        }
        .container .border-div {
            width: 200%;
        }
    }