            .faq {
                padding-top: 3em;
            }
            
            .faq-container {
                display: flex;
                justify-content: center;
                background: #011d40;
            }
            
            .faq header {
                color: white;
            }
            
            .support-container {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                padding: 3em;
                padding-top: 2em;
            }
            
            .question-container {
                width: 50%;
                color: white;
            }
            
            .question-container .question {
                font-weight: bold;
                font-size: 22px;
                font-family: 'Montserrat', sans-serif;
                font-weight: 800;
            }
            
            .question-answer {
                border-bottom: 0.5px solid #199bdc;
                font-size: 16px;
                font-family: 'Montserrat', sans-serif;
                font-weight: 200;
            }
            
            .contact-support {
                width: 40%;
                color: white;
            }
            
            .contact-support h1 {
                font-size: 22px;
                font-family: 'Montserrat', sans-serif;
                font-weight: 700;
            }
            
            .contact-support p {
                font-size: 16px;
                font-family: 'Montserrat', sans-serif;
                font-weight: 400;
            }
            
            .contact-support a {
                text-decoration: none;
            }
            
            .contact-support p span {
                color: #199bdc;
            }
            
            .question-container p a {
                text-decoration: none;
            }
            
            .question-container p span {
                color: #199bdc;
                font-family: 'Montserrat', sans-serif;
            }
            
            @media screen and (max-width: 800px) {
                .support-container {
                    flex-direction: column;
                }
                .question-container {
                    width: 100%;
                }
                .contact-support {
                    padding-top: 2em;
                    width: 100%;
                }
                .contact-support h1 {
                    font-size: 18px;
                }
            }
            
            .container .btn {
                display: inline-block;
                margin-bottom: 20px;
                border: 1px solid #1569a8;
                background: white;
                /* color: #1569a8; */
                /* padding: 5px 10px; */
                border-radius: 5px;
                margin-right: 5px;
                font-size: 15px;
                cursor: pointer;
                outline: none;
            }
            
            .container .btn:hover {
                background: #1569a8;
                color: white;
                transition: 0.5s;
            }
            
            .accordion {
                /* margin: 0 auto; */
                width: 95%;
            }
            
            .accordion button {
                border: none;
                background: none;
                text-align: start;
            }
            
            .accordion .title {
                position: relative;
                display: block;
                padding: 1em;
                padding-left: 2em;
                left: 0em;
                color: white;
                cursor: pointer;
                transition: all 0.4s cubic-bezier(0.865, 0.14, 0.095, 0.87);
                font-size: 17px;
                align-items: center;
            }
            
            .accordion .title::after {
                content: "+";
                font-size: 18px;
                color: white;
                transition: transform 0.5s ease-in-out;
                position: absolute;
                left: 0px;
                font-family: monospace;
            }
            
            .accordion .title.is-expanded {
                transition: background 0.5s;
                /* background: #0e4671; */
            }
            
            .accordion .title.is-expanded::after {
                content: "-";
                transform: rotate(-360deg);
            }
            
            .accordion .content {
                font-family: 'Montserrat', sans-serif;
                font-size: 18px;
                overflow: hidden;
                height: 0px;
                position: relative;
                padding: 0 1.5em;
                box-shadow: inset 4px 0 0 0 white, inset 0 3px 6px rgba(0, 0, 0, 0.75);
                background: #444;
                background: linear-gradient(to bottom, #172e67 0%, #0b2b7d 100%);
                color: white;
                transition: height 0.4s cubic-bezier(0.865, 0.14, 0.095, 0.87);
                right: 40px
            }
            
            .accordion .content p {
                padding: 30px 0;
                margin: 0;
                opacity: 0;
                transition: 0.5s;
            }
            
            .accordion .content.is-expanded {
                height: 210px;
                overflow: hidden;
            }
            
            .accordion .content.is-expanded p {
                opacity: 1;
            }
            
            @media screen and (max-width: 800px) {
                .accordion .title {
                    font-size: 16px;
                }
                .accordion .content {
                    font-size: 12px;
                }
                .accordion {
                    /* margin: 0 auto; */
                    width: 105%;
                }
                .accordion .content.is-expanded {
                    height: 220px;
                    overflow: hidden;
                }
            }