nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: nowrap;
    height: 4em;
    z-index: 1;
    /* position: fixed; */
    width: 100%;
    padding: 0 10rem;
}

.background {
    background-color: #172e67;
}

@media screen and (max-width: 1050px) {
    nav {
        padding: 0 1rem;
    }
}

@media screen and (max-width: 600px) {
    nav {
        position: absolute;
    }
    .logo-button .mobile-logo {
        margin-top: 20px;
    }
}

nav button {
    border: none;
    background: transparent;
    color: white;
    height: 2em;
    cursor: pointer;
    flex-wrap: nowrap;
    font-size: 15px;
    font-weight: 400;
}

.x-button {
    display: none;
}

.logo-button {
    height: 100%;
    padding: 0 2em;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

button img {
    height: 50%;
}

.border {
    background: #011d40;
    border: 1px solid #199bdc;
    border-radius: 10px;
    /* padding: 0.5em 1.5em; */
    height: 2em;
    align-items: center;
    justify-content: center;
}

.border div {
    font-size: 11px;
    white-space: nowrap;
    font-weight: 500;
}

.profile-group {
    width: 15%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.profile-group button {
    margin: 0.2em;
    padding: 0.5em;
    height: 3em;
}

.profile-button {
    margin: 0.2em;
    padding: 0.5em;
    height: 3em;
}

.img-button img {
    height: 1.4em;
}

@media screen and (max-width: 600px) {
    nav {
        display: block;
        height: auto;
        text-align: center;
        padding: 0 2em;
        background-color: #172e67;
        position: fixed;
        transition: all 0.5s ease-in-out;
    }
    nav .mobile-header-container .logo-button {
        position: relative;
        display: none;
    }
    .mobile-header-container {
        display: flex;
        align-items: center;
        justify-content: space-around;
        background-color: red;
    }
    .profile-button {
        padding-left: 30px;
    }
    .logo-button {
        height: 4em;
        width: 40%;
        padding: 0;
        position: absolute;
        border: none;
        top: 0;
    }
    nav button {
        width: 100%;
        border-bottom: 1px solid #199bdc;
        height: 5em;
        margin: 0;
    }
    nav button:nth-child(3) {
        margin-top: 6em;
        border-top: 1px solid #199bdc;
    }
    .border {
        width: auto;
        height: 3em;
    }
    .x-button {
        display: inline-block;
        position: absolute;
        top: 1em;
        left: 1em;
        padding: 1em;
        height: 3em;
    }
    .fatimes {
        transform: scale(1.4);
    }
    .p-button {
        display: inline-block;
        position: absolute;
        /* top: 1em; */
        right: 1em;
        padding: 1em;
        height: 3em;
        margin-bottom: 5px;
    }
    .x-button img {
        height: 1em;
    }
    .profile-group {
        margin-top: 1.5em;
        width: 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
    }
    .img-button {
        padding: 0.7em;
        height: 3em;
        width: 3em;
    }
    .profile-group .img-button {
        padding: 0.7em;
    }
    .img-button img {
        height: 1.4em;
    }
    .profile-button {
        position: absolute;
        top: 1em;
        right: 1em;
    }
}