.partners-container {
    height: auto;
    width: '100%';
    background-color: #199bdc;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.partners-header {
    padding-top: 10px;
    color: white;
}

.partners-header h1 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 30px;
    text-align: start;
}

.logos-container {
    align-items: center;
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
}

.logos-container img {
    transform: scale(0.8);
    padding: 3em;
}