* {
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}

@media screen and (min-width: 1200px) {
    .large-container {
        margin-top: 5%;
    }
    .mobile-container {}
}

.submit-button {
    z-index: 100000;
}

.submit-button:hover {
    transform: scale(1.1);
    transition: all .5s ease-in-out;
    z-index: 10000;
}

.large-container .leftside {
    position: absolute;
    height: 400px;
    width: 500px;
    left: 180px;
    margin-top: 180px;
}

.large-container .rightside {
    position: absolute;
    height: 400px;
    width: 500px;
    right: 200px;
    margin-top: 10px;
}

.large-container .fade-wrapper {
    transition: opacity ease-in-out;
}

.large-container .fade-out {
    opacity: 0;
}

.large-container .fade-in {
    opacity: 1;
}

.large-container .fadeIn {
    opacity: 1;
    animation-name: fadeInOpacity;
    animation-iteration-count: 1;
    animation-timing-function: ease-in-out;
    animation-duration: 1.5s;
}

.large-container .fadeOut {
    opacity: 1;
    animation-name: fadeOut;
    animation-iteration-count: 0;
    animation-timing-function: ease-in-out;
    animation-duration: 1.5s;
}

@keyframes fadeInOpacity {
    0% {
        opacity: 0;
    }
    75% {
        opacity: 0.75;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }
    75% {
        opacity: 0.75;
    }
    100% {
        opacity: 0;
    }
}

