    right: 80px;
    margin-top: 300px;
    cursor: pointer;
    height: 80px;
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    color: white;
    z-index: 100;
}

.forward:hover {
    transform: scale(0.8);
    transition: all 0.6s ease-in-out;
}

.back {
    position: absolute;
    left: 80px;
    margin-top: 300px;
