.more-about-us {
    background: #021e3f;
    /* height: 20em; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 3.5em 8em;
}

.more-information {
    color: #199bdc;
    border: 0.5px solid #199bdc;
    padding: 2em;
    margin: 2em;
    max-width: 350px;
    height: 300px;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.more-information img {
    width: 22%;
}

.more-information h2 {
    font-size: 1.2em;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    letter-spacing: 0.1px;
    font-size: 16px;
}

.more-information p {
    text-align: center;
    line-height: 2em;
    margin-top: -10px;
    width: 70%;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    font-size: 14px;
}

.bull-bear-icons {
    display: flex;
    justify-content: space-evenly;
}

@media screen and (max-width: 1220px) and (min-width: 700px) {
    .more-information {
        padding: 1em;
        margin: 1em;
    }
}

@media screen and (max-width: 700px) {
    .more-about-us {
        flex-direction: column;
        padding: 2em;
    }
    .more-information {
        max-width: unset;
        padding: 1em;
        margin: 1em;
        flex: 1;
    }
}