::-webkit-scrollbar {
    display: none;
}

* {
    box-sizing: border-box;
    font-family: 'Avenir';
}

html,
body {
    max-width: 100%;
    overflow-x: hidden;
    overflow-y: scroll
}

.backgroung-div {
    background: url('pages/landing/icons/background.png');
    background-size: cover;
    height: 8em;
}

header {
    padding: 3em;
    padding-bottom: 0.5em;
}

h1 {
    display: inline-block;
    font-size: 1.6em;
}

.border-div {
    display: flex;
    flex-direction: row;
    height: 0.8em;
}

.light-red {
    width: 20%;
    background: #F01619;
}

.dark-red {
    width: 20%;
    background: #C11B29;
}

.gray {
    width: 20%;
    background: #434652;
}

.dark-blue {
    width: 20%;
    background: #203D87;
}

.light-blue {
    width: 20%;
    background: #4264B1;
}

@media screen and (max-width: 600px) {
    .border-div {
        width: 200%;
    }
}